.contact {
    padding: 4rem 0;
    background-color: var(--contact-background-color);
}

.contact .container {
    max-width: 1300px;
    margin: 0 auto;
    padding: 0 1rem;
}

.contact-title {
    font-family: "Nunito", sans-serif;
    color: var(--contact-title-color);
    font-size: 2.2rem;
    text-align: center;
    position: relative;
    margin-bottom: 3.5rem;
}

.contact-title::after {
    content: '';
    width: 3%;
    height: 4px;
    background: var(--contact-title-color);
    position: absolute;
    left: 50%;
    bottom: -10px;
    transform: translateX(-50%);
}

.contact-content {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    gap: 1.5rem;
    flex-wrap: wrap;
}

.contact-form {
    flex: 1;
    background: var(--contact-form-background);
    padding: 2rem;
    border-radius: 8px;
}

.contact-form label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: bold;
    color: var(--contact-form-label-color);
}

.contact-form input,
.contact-form textarea {
    width: 100%;
    padding: 0.75rem;
    margin-bottom: 1rem;
    border: 1px solid var(--contact-form-input-border);
    border-radius: 4px;
    font-size: 1rem;
    background-color: var(--contact-form-input-background);
    color: var(--contact-form-input-color);
}

.contact-form button {
    background-color: var(--contact-form-button-background);
    color: var(--contact-form-button-color);
    padding: 0.75rem 1.5rem;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1.1rem;
    transition: background 0.3s ease;
    border: none;
}

.contact-form button:hover {
    background: var(--contact-form-button-hover-background);
}

.contact-info {
    flex: 1;
    background: var(--contact-info-background);
    padding: 2rem;
    border-radius: 8px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.contact-info-title {
    font-family: "Nunito", sans-serif;
    font-size: 2.2rem;
    margin-bottom: 1.5rem;
    color: var(--contact-info-title-color);
}

.contact-info p {
    margin: 0.5rem 0;
    font-size: 1.2rem;
    color: var(--contact-info-text-color);
}

.contact-info .contact-icon {
    font-size: 2rem;
    margin-right: 1rem;
    vertical-align: middle;
    color: var(--contact-info-icon-color);
}

.contact-info a {
    color: var(--contact-info-link-color);
    text-decoration: none;
    transition: color 0.3s ease;
}

.contact-info a:hover {
    color: var(--contact-info-link-hover-color);
}

.contact-social-icons {
    display: flex;
    justify-content: center;
    margin-top: 2rem;
}

.contact-social-icons a {
    color: var(--contact-info-link-color);
    margin: 0 0.5rem;
    font-size: 2.5rem;
    transition: color 0.3s ease;
}

.contact-social-icons a:hover {
    color: var(--contact-info-link-hover-color);
}

.go-to-top {
    margin-top: 4rem;
    background: transparent;
    color: var(--go-to-top-color);
    font-size: 2.5rem;
    cursor: pointer;
    transition: transform 0.3s ease, color 0.3s ease;
    animation: moveUpDown 2s infinite;
    display: flex;
    justify-content: center;
    align-items: center;
}

.go-to-top:hover {
    color: var(--go-to-top-hover-color);
    animation: none;
}

.go-to-top-wrapper {
    display: flex;
    justify-content: center;
    margin-top: 0rem;
}

@keyframes moveUpDown {
    0%, 100% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-10px);
    }
}

@media (max-width: 768px) {
    .contact-content {
        flex-direction: column;
    }

    .contact-info,
    .contact-form {
        width: 100%;
        margin-bottom: 0rem;
    }

    .go-to-top-wrapper {
        margin-top: 0rem;
    }
}
