.about {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 4rem 0;
    position: relative;
    background-color: var(--about-background-color);
}

.about .container {
    max-width: 1300px;
    margin: 0 auto;
    padding: 0 1rem;
}

.about-title {
    font-family: "Nunito", sans-serif;
    font-size: 2.2rem;
    text-align: center;
    position: relative;
    margin-bottom: 3.5rem;
    color: var(--about-title-color);
}

.about-title::after {
    content: '';
    width: 3%;
    height: 4px;
    background: var(--about-title-color);
    position: absolute;
    left: 50%;
    bottom: -10px;
    transform: translateX(-50%);
}

.about-content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.about-image-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 310px;
    height: 310px;
    border-radius: 50%;
    border: 8px solid var(--about-image-border-color);
    overflow: hidden;
    margin-bottom: 1rem;
}

.about-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.about-text {
    font-size: 1.3rem;
    flex: 2;
    color: var(--about-text-color);
}

.resume-button {
    display: inline-block;
    margin: 0 auto;
    padding: 0.5rem 1rem;
    background-color: var(--resume-button-bg);
    color: var(--resume-button-text-color);
    text-align: center;
    text-decoration: none;
    border-radius: 5px;
    font-size: 1rem;
    transition: background-color 0.3s ease, transform 0.2s;
    border: none;
    cursor: pointer;
}

.resume-button:hover {
    background-color: var(--resume-button-hover-bg);
}

@media (min-width: 768px) {
    .about-content {
        flex-direction: row;
        text-align: center;
    }
    .about-image-wrapper {
        margin-right: 5rem;
    }
    .about-text {
        margin-top: 0;
        text-align: center;
        word-spacing: 3px;
        line-height: 2.3rem;
    }
    .resume-button {
        margin-top: 2rem;
    }
}

@media (max-width: 767px) {
    .about-image-wrapper {
        display: none;
    }
    .about-text {
        margin-top: 0;
        text-align: center;
        word-spacing: 2px;
        line-height: 2.2rem;
    }
    .resume-button {
        margin-top: 2rem;
    }
}
