/* CSS Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: "Nunito", sans-serif;
    margin: 0;
    background-color: var(--background-color);
    color: var(--text-color);
}

/* Custom Styles */
.container {
    width: 100%;
    max-width: 1300px;
    margin: 0 auto;
    padding: 0 1rem;
}

.button {
    background-color: var(--button-bg);
    color: var(--button-text-color);
    padding: 0.5rem 0.8rem;
    text-align: center;
    text-decoration: none;
    border-radius: 5px;
    font-size: 1.2rem;
    transition: background-color 0.3s ease, transform 0.2s;
    border: none;
    cursor: pointer;
}

.button:hover {
    background-color: var(--button-hover-bg);
}

/* Light/Dark Mode Variables */
:root {
    --background-color: #fff;
    --text-color: #000;
    --header-bg: rgba(255, 255, 255, 0.9);
    --header-color: #333;
    --hero-background: linear-gradient(140deg, #f0f4f8, #d9e2ec, #bcccdc, #9fb3c8, #829ab1); /* Light mode gradient */
    --hero-text-color: #333;
    --about-background-color: #f5f5f5;
    --about-title-color: #333;
    --about-image-border-color: #124E66;
    --about-text-color: #333;
    --resume-button-bg: #124E66;
    --resume-button-text-color: #D3D9D4;
    --resume-button-hover-bg: #748D92;
    --skills-background-color: #f9f9f9;
    --skills-title-color: #333;
    --skills-text-color: #333;
    --projects-background-color: #f0f0f0;
    --projects-title-color: #333;
    --project-card-background: #fff;
    --project-card-border: #ddd;
    --project-title-color: #333;
    --project-type-color: #777;
    --project-description-color: #555;
    --project-languages-color: #555;
    --language-button-background: #124E66;
    --language-button-color: #D3D9D4;
    --project-link-color: #0fa4af;
    --project-link-hover-color: #124E66;
    --contact-background-color: #f5f5f5;
    --contact-title-color: #333;
    --contact-form-background: #fff;
    --contact-form-label-color: #333;
    --contact-form-input-border: #ddd;
    --contact-form-input-background: #fff;
    --contact-form-input-color: #000;
    --contact-form-button-background: #124E66;
    --contact-form-button-color: #D3D9D4;
    --contact-form-button-hover-background: #748D92;
    --contact-info-background: #fff;
